import { Page } from './Page';
import { myRequest, el } from '../util/util';
export var Login = new Page({
    tpl: require('../views/login.html'),
    mounted: function (_con, route) {
        var userBtn = el('userLogin');
        userBtn.addEventListener('click', function () {
            var val = el('user_name').value;
            if (val.replace(/\s/g, '') === '') {
                alert('');
                return;
            }
            myRequest('User/login', {
                Account: val,
            }).then(function (data) {
                if (data.Code === 0) {
                    alert('');
                    route.goto('projects');
                }
            });
        });
    },
});
